h1 {
  font-family: 'Indie Flower', cursive; /* Utiliser une police de caractères cursive pour un aspect de graffiti */
  font-size: 30px; /* Taille de police plus grande pour un aspect audacieux */
  color: #ff4500; /* Texte en orange */
  bottom: 100px;
  /* Ajoutez d'autres styles selon vos besoins */
}

li {
  list-style-type: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

/* Style.css */
.image-container {
  position: relative;
}

/* Style.css */
.banner {
  position: absolute;
  top: -275px; /* Déplace la bannière vers le haut de 50 pixels */
  left: -10px;
  width: 100.95%;
  height: 45vh; /* 50% de la hauteur de la vue (viewport) */
  z-index: 1;
}

.fond-gif {
  position: absolute;
  top: 148px; /* Déplace la bannière vers le haut de 50 pixels */
  left: -10px;
  width: 100.97%;
  height: 85vh; /* 50% de la hauteur de la vue (viewport) */
  z-index: 1;
  opacity: 0.9; /* Définit l'opacité à 80% */
}

.App-logo {
  position: absolute;
  top: 50px;
  left: 3%;
  transform: translate(-50%, -50%);
  width: 7%;
  z-index: 2;
}

.logo_bis {
  position: absolute;
  top: -120px; /* Centrer verticalement */
  left: 50%; /* Centrer horizontalement */
  transform: translate(-50%, -50%); /* Centrer l'image */
  width: 50%;
  z-index: 1; /* Placer le logo au-dessus de la bannière */
}

.header-banner {
  /*border: 8px solid #f8b4dc; /* Bordure */
  border-radius: 7px;
  background-color: #f8b4dc;
  background-image: url('../assets/header_background.jpg'); /* Image de fond */
  background-size: cover; /* Ajuster la taille de l'image pour couvrir la boîte */
  background-position: center;
  position: absolute;
  top: 25px;
  left: 31.7%;
  width: 800px;
  height: 46px;
  font-size: 30px;
  z-index: 2;
}

/* Style.css */
.nav-link-home {
  position: absolute;
  text-decoration: none;
  top: 50px; /* Déplace la navigation vers le bas de 50 pixels pour apparaître sur la bannière */
  left: 35%;
  transform: translate(-50%, -50%);
  color: #080808;
  font-size: 30px;
  z-index: 2;
}

.nav-link-about {
  text-decoration: none;
  position: absolute;
  top: 50px;
  left: 70%;
  z-index: 2;
  transform: translate(-50%, -50%);
  color: #080808;
  font-size: 30px;
  cursor: pointer;
}

.nav-link-actus {
  text-decoration: none;
  position: absolute;
  top: 50px;
  left: 45%;
  z-index: 2;
  transform: translate(-50%, -50%);
  color: #080808;
  font-size: 30px;
}

.nav-link-rejoindre {
  text-decoration: none;
  position: absolute;
  top: 50px;
  left: 52%;
  transform: translate(10%, -50%);
  z-index: 2;
  color: #080808;
  font-size: 30px;
}

.nav-link-rejoindre:hover {
  color: #be006f;
  animation: hover 1s linear infinite;
}

.nav-link-actus:hover {
  color: #be006f;
  animation: hover 1s linear infinite;
}

.nav-link-about:hover {
  color: #be006f;
  animation: hover 1s linear infinite;
}

.nav-link-home:hover {
  color: #be006f;
  animation: hover 1s linear infinite;
}

@keyframes hover {
  0% {
    font-size: 30px;
  }
  20% {
    font-size: 31px;
  }
  40% {
    font-size: 32px;
  }
}

.carousel {
  width: 80%; /* Largeur du carrousel */
  margin: 0 auto;
}

.titre_accueil {
  color: #ea0683;
  font-size: 80px;
  text-align: center; /* Texte centré */
  position: absolute; /* Position absolue pour centrer */
  top: 260px; /* Déplacer vers le haut de 50% de la hauteur de la fenêtre */
  left: 0%; /* Déplacer vers la gauche de 50% de la largeur de la fenêtre */
  transform: translate(-50%, -50%);
  width: 100%; /* Largeur de chaque élément de texte */
  white-space: nowrap; /* Empêcher le texte de passer à la ligne */
  animation: float 5s linear infinite;
  z-index: 2;
}

.texte_accueil {
  color: #d50084;
  font-size: 30px;
  text-align: center;
  position: absolute; /* Position absolue pour centrer */
  top: 500px; /* Déplacer vers le haut de 50% de la hauteur de la fenêtre */
  left: 50%; /* Déplacer vers la gauche de 50% de la largeur de la fenêtre */
  transform: translate(-50%, -50%);
  width: 100%; /* Largeur de chaque élément de texte */
  white-space: nowrap; /* Empêcher le texte de passer à la ligne */
  z-index: 2;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

